.popup__input {
    border-top: none;
    border-left: none;
    border-right: none;
    width: 100%;
    height: 50px;
}

.popup__input:invalid {
    border-bottom: 1px solid #C4C4C4;
    color: #C4C4C4;
}


.popup__input:valid {
    color: black;
    border-bottom: 1px solid rgba(0, 0, 0, 0.2);
}